"use client";

import { useState } from "react";
import { useForm } from "@tanstack/react-form";
import { Button } from "@trading-platform/ui/button";
import { Input } from "@trading-platform/ui/input";
import { Label } from "@trading-platform/ui/label";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@trading-platform/ui/select";

interface WaitlistFormB2BProps {
  source?: string;
  className?: string;
}

export function WaitlistFormB2B({
  source = "landing_page_b2b",
  className,
}: WaitlistFormB2BProps) {
  const [loading, setLoading] = useState(false);
  const [success, setSuccess] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const form = useForm({
    defaultValues: {
      email: "",
      full_name: "",
      phone: "",
      restaurant_name: "",
      number_of_locations: "",
    },
    onSubmit: async ({ value }) => {
      setLoading(true);
      setError(null);

      try {
        const response = await fetch("/api/waitlist", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            email: value.email,
            full_name: value.full_name,
            phone: value.phone,
            restaurant_name: value.restaurant_name,
            number_of_locations: parseInt(value.number_of_locations) || null,
            role: "owner",
            source,
          }),
        });

        const data = await response.json();

        if (!response.ok) {
          throw new Error(data.error || "Failed to join waitlist");
        }

        setSuccess(true);
      } catch (err) {
        setError(err instanceof Error ? err.message : "Something went wrong");
      } finally {
        setLoading(false);
      }
    },
  });

  if (success) {
    return (
      <div className={className}>
        <div className="rounded-lg border border-green-500 bg-green-50 p-6 text-center dark:bg-green-950">
          <div className="mb-2 text-4xl">ðŸš€</div>
          <h3 className="mb-2 text-xl font-bold text-green-900 dark:text-green-100">
            Welcome to the future of restaurant management!
          </h3>
          <p className="text-green-800 dark:text-green-200">
            We&apos;ll contact you within 48 hours to schedule your personalized
            demo.
          </p>
          <p className="mt-3 text-sm font-semibold text-green-700 dark:text-green-300">
            Early access benefits:
          </p>
          <ul className="mt-2 space-y-1 text-sm text-green-700 dark:text-green-300">
            <li>âœ“ 3 months completely free ($450 value per location)</li>
            <li>âœ“ White-glove onboarding + POS CSV analysis</li>
            <li>âœ“ Direct line to our founding team</li>
            <li>âœ“ Shape the product with your feedback</li>
          </ul>
        </div>
      </div>
    );
  }

  return (
    <div className={className}>
      <form
        onSubmit={(e) => {
          e.preventDefault();
          e.stopPropagation();
          form.handleSubmit();
        }}
        className="space-y-4"
      >
        <form.Field
          name="email"
          validators={{
            onChange: ({ value }) => {
              if (!value) return "Email is required";
              if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(value))
                return "Invalid email address";
              return undefined;
            },
          }}
        >
          {(field) => (
            <div className="space-y-2">
              <Label htmlFor="email">
                Work Email <span className="text-red-500">*</span>
              </Label>
              <Input
                id="email"
                type="email"
                placeholder="you@restaurant.com"
                value={field.state.value}
                onChange={(e) => field.handleChange(e.target.value)}
                onBlur={field.handleBlur}
                disabled={loading}
                required
              />
              {field.state.meta.errors && (
                <p className="text-sm text-red-500">
                  {field.state.meta.errors[0]}
                </p>
              )}
            </div>
          )}
        </form.Field>

        <form.Field
          name="full_name"
          validators={{
            onChange: ({ value }) => {
              if (!value) return "Full name is required";
              return undefined;
            },
          }}
        >
          {(field) => (
            <div className="space-y-2">
              <Label htmlFor="full_name">
                Full Name <span className="text-red-500">*</span>
              </Label>
              <Input
                id="full_name"
                type="text"
                placeholder="John Doe"
                value={field.state.value}
                onChange={(e) => field.handleChange(e.target.value)}
                onBlur={field.handleBlur}
                disabled={loading}
                required
              />
              {field.state.meta.errors && (
                <p className="text-sm text-red-500">
                  {field.state.meta.errors[0]}
                </p>
              )}
            </div>
          )}
        </form.Field>

        <form.Field name="phone">
          {(field) => (
            <div className="space-y-2">
              <Label htmlFor="phone">Phone Number</Label>
              <Input
                id="phone"
                type="tel"
                placeholder="+90 555 123 4567"
                value={field.state.value}
                onChange={(e) => field.handleChange(e.target.value)}
                onBlur={field.handleBlur}
                disabled={loading}
              />
            </div>
          )}
        </form.Field>

        <form.Field
          name="restaurant_name"
          validators={{
            onChange: ({ value }) => {
              if (!value) return "Restaurant name is required";
              return undefined;
            },
          }}
        >
          {(field) => (
            <div className="space-y-2">
              <Label htmlFor="restaurant_name">
                Restaurant Name <span className="text-red-500">*</span>
              </Label>
              <Input
                id="restaurant_name"
                type="text"
                placeholder="Bella Vista Restaurant"
                value={field.state.value}
                onChange={(e) => field.handleChange(e.target.value)}
                onBlur={field.handleBlur}
                disabled={loading}
                required
              />
              {field.state.meta.errors && (
                <p className="text-sm text-red-500">
                  {field.state.meta.errors[0]}
                </p>
              )}
            </div>
          )}
        </form.Field>

        <form.Field
          name="number_of_locations"
          validators={{
            onChange: ({ value }) => {
              if (!value) return "Number of locations is required";
              if (parseInt(value) < 1) return "Must be at least 1";
              return undefined;
            },
          }}
        >
          {(field) => (
            <div className="space-y-2">
              <Label htmlFor="number_of_locations">
                Number of Locations <span className="text-red-500">*</span>
              </Label>
              <Input
                id="number_of_locations"
                type="number"
                min="1"
                placeholder="1"
                value={field.state.value}
                onChange={(e) => field.handleChange(e.target.value)}
                onBlur={field.handleBlur}
                disabled={loading}
                required
              />
              {field.state.meta.errors && (
                <p className="text-sm text-red-500">
                  {field.state.meta.errors[0]}
                </p>
              )}
            </div>
          )}
        </form.Field>

        {error && (
          <div className="rounded-md border border-red-300 bg-red-50 p-3 text-sm text-red-800 dark:bg-red-950 dark:text-red-200">
            {error}
          </div>
        )}

        <Button type="submit" className="w-full" disabled={loading}>
          {loading ? "Joining..." : "Join Early Access"}
        </Button>

        <p className="text-center text-xs text-gray-500">
          By joining, you agree to our Terms of Service and Privacy Policy
        </p>
      </form>
    </div>
  );
}
